.footer {
  padding: 40px 0;
  background-color: var(--footer-bg-color);
}
.logo {
  text-decoration: none;
  color: var(--yellow-text-color);
  font-family: Film P3;
  font-weight: Bold;
  font-size: 40px;
  line-height: 1;
  letter-spacing: 0.03em;
}
.footer-nav {
  display: flex;
  justify-content: space-evenly;
}
.footer-nav__list {
  list-style: none;
  padding: 0;
  margin: 0 0 32px 0;
}

.footer-nav__item {
  &:not(:last-child) {
    margin-bottom: 16px;
  }
}
.footer-nav__link {
  text-decoration: none;
  color: var(--yellow-text-color);
}
.footer-social-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  gap: 20px;
  justify-content: flex-end;
}

.footer-social-list__item {
  padding: 0;
  margin: 0;
}
.footer-social-list__link {
  fill: var(--yellow-text-color);
}
